<style>
/*  */
.el-scrollbar__wrap {
  overflow-x: hidden !important;
}
/* element */
.el-scrollbar__bar {
  position: absolute;
  right: 2px;
  bottom: 2px;
  z-index: 1;
  border-radius: 4px;
  opacity: 1;
  -webkit-transition: opacity 120ms ease-out;
  transition: opacity 120ms ease-out;
}
.vachange_tables {
  margin-top: 12px;
}
.vachange_tables table tr th {
  font-size: 15px;
  font-weight: normal;
  color: #ccc;
}
.vachangemain {
  width: 95%;
  margin: 0 auto;
}
.title {
  height: 20px;
  width: 100%;
}
.tops {
  width: 100%;
  margin-top: 12px;
}
.tablemain {
  width: 100%;
}
.tablemain table td {
  text-align: center;
}
</style>
<template>
  <div class="vachangemain">
    <div class="tjfx_tool">
      <div class="tjfx_bar">
        <i class="icon-changzhanzs"></i>
        
      </div>
      <div class="tjfx_tools">
        <el-select
          v-model="value1"
          multiple
          collapse-tags
          style="margin-left: 20px;"
          placeholder=""
        >
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
        <el-select v-model="value2" collapse-tags style="margin-left: 20px;" placeholder="">
          <el-option
            v-for="item in options1"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
        <el-button style="margin-left:20px;" @click='getData()'></el-button>
      </div>
    </div>
    <div class="tops">
        <el-table :data="tableData" style="width: 100%" size="mini" border class="vachange_tables">
          <el-table-column type='index' label=""></el-table-column>
          <el-table-column prop="lineName" label=""></el-table-column>
          <el-table-column prop="localName" label=""></el-table-column>
          <el-table-column prop="lineId" label="id"></el-table-column>
          <el-table-column prop="cecThreshold" label=""></el-table-column>
          <el-table-column prop="volThreshold" label=""></el-table-column>
          <el-table-column prop="powThreshold" label=""></el-table-column>
          <el-table-column label="">
               <template slot-scope="scope">
                <div>
                  <el-button size='small' icon="el-icon-edit" @click="close=true;obj=scope.row"></el-button>
                  <el-button size='small'  type="danger" icon="el-icon-delete" @click="obj=scope.row;deletedData()"></el-button>
                </div>
              </template>
          </el-table-column>
        </el-table>
    </div>
    <overlay :close.sync="close">
      <div class="add_trans">
        <div class="sblb_bar">
          <i class="icon-changzhanzs"></i>
          {{obj.localName}}
          <div class="fr sbbt_tool">
            <i class="icon-error sblbguabi" @click="close=false"></i>
          </div>
        </div>
        <div class="add_main">
          <el-form ref="form" label-position="left" label-width="90px">
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="obj.volThreshold" placeholder="" style="width:220px;"></el-input>
            </el-form-item>
             <el-form-item label="">
              <el-input v-model="obj.cecThreshold" placeholder="" style="width:220px;"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="obj.powThreshold" placeholder="" style="width:220px;"></el-input>
            </el-form-item>
            <!-- <el-form-item label="">
              <el-date-picker type="date" placeholder="" v-model="form.date"  ></el-date-picker>
            </el-form-item> -->
              <el-button type="primary" @click="saveData()"></el-button>
              <el-button @click="close=false"> </el-button>
            </el-form-item>
          </el-form>
        </div>
      </div>
    </overlay>
  </div>
</template>

<script>
import overlay from "@/components/overlay";
export default {
  data() {
    return {
      isFull: false,
      tableData: [
        {
          date: "",
          name: "_11#"
        },
        {
          date: "",
          name: "ZGS11"
        },
        {
          date: "",
          name: "630"
        },
        ,
        {
          date: "",
          name: "957.19"
        },
        {
          date: "",
          name: "2009/04/10"
        },
        {
          date: "/",
          name: ""
        },
        {
          date: "",
          name: "2"
        },
        {
          date: "",
          name: "_10kV16"
        },
        {
          date: "",
          name: "9.55"
        },
        {
          date: "",
          name: "Dyn11"
        }
      ],
      options: [
        {
          value: "1",
          label: ""
        }
      ],
      value1: ["1"],
      options1: [
        {
          value: "530C24CD-1CFC-478B-B287-35FDEF7BFDE6-64950",
          label: "_10kV16"
        }
      ],
      value2: "530C24CD-1CFC-478B-B287-35FDEF7BFDE6-64950",
      obj:{},
      close:false
    };
  },
  mounted() {
    this.getData()
  },
  methods: {
    cancel() {
      this.$emit("cancel", false);
    },
    getData(){
      this.$http.get('/lineMonitor/getWarningThresholdList',{
        params:{
          lineId:this.value2
        }
      }).then(res=>{
        if(res.data.code ===1){
          this.tableData=res.data.data
        }
      })
    },
    saveData(){
      this.$http.post('lineMonitor/updateWarningThreshold',{
        equipmentId:this.obj.equipmentId,
        volThreshold:this.obj.volThreshold,
        powThreshold:this.obj.cecThreshold,
        cecThreshold:this.obj.powThreshold,
        lineId:this.obj.lineId,
        id:this.obj.id
      }).then(res=>{
        if(res.data.code ===1 ){
          this.close = false;
          this.getData()
          this.$message({
          message: '',
          type: 'success'
        });
        }
      })
    },
    deletedData(){
      this.$confirm('', '', {
          distinguishCancelAndClose: true,
          confirmButtonText: '',
          cancelButtonText: ''
        }).then(()=>{
            this.$http.post('/lineMonitor/deleteWarningThreshold?id='+this.obj.id).then(res=>{
          if(res.data.code ===1){
           this.getData()
           this.$message({
             message: '',
              type: 'success'
            });
          }
          })
      })
    }
  },
  components:{overlay}
};
</script>

